import React, { useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { PageSettings } from "../../config/page-settings";
import background from "../../assets/img/background.jpg";
import { Card, CardBody } from "reactstrap";
import userIcon from "../../assets/img/user.gif";
import bakiIcon from "../../assets/img/baki.gif";
import laporanIcon from "../../assets/img/laporan.gif";
import reprintIcon from "../../assets/img/reprint.gif";
import tutupTokoIcon from "../../assets/img/tutup_toko.gif";
import { ToastNotification } from "../../components/helpers/notification";
import { postDataNoTImeOut } from "../../config/axios";
import Swal from "sweetalert2";

const activeBg = background;
const SubMenuUtility = () => {
  const type = useContext(PageSettings);
  const history = useHistory();
  //   const [dateSystem, setdateSystem] = useState(true);
  const version = process.env.REACT_APP_VERSION;
  const url = process.env.REACT_APP_URL;
  const routeChange = (path) => {
    history.push(path);
  };
  const showModalTutupToko = () => {
    Swal.fire({
      html: "<h2><b>Yakin Ingin Menutup Toko ?<br/> ",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Close",
      showCancelButton: true,
      confirmButtonText: "Tutup Toko",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        postDataNoTImeOut("system/close", { PROSES: "close" })
          .then((res) => {
            ToastNotification("success", "Berhasil Menutup Toko");

            setTimeout(() => {
              // window.location.reload();
              window.location.href = version === "lite" ? url : "/nagapos.com";
              localStorage.clear();
              localStorage.setItem("islogin", "false");
            }, 1000);
          })
          .catch((err) => {
            // console.log(err.response);
            err.response === undefined
              ? ToastNotification(
                  "info",
                  err.response === undefined
                    ? "Terjadi Kesalahan Saat Request Data"
                    : err.response.data
                )
              : err.response.data === "Invalid token."
              ? this.handleInvalidToken()
              : ToastNotification("info", err.response.data);
            //   setTimeout(() => {
            //   window.location.reload();
            //   localStorage.clear();
            //   localStorage.setItem("islogin", "false");
            // }, 1000);
          });
      }
    });
  };
  useEffect(() => {
    type.handleSetPageSidebar(false);
    type.handleSetPageHeader(false);
    // setdateSystem((getUserdata()?.tgl_system || "-") === getToday());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <React.Fragment>
      <div className="login-cover">
        <div
          className="login-cover-image"
          style={{ backgroundImage: "url(" + activeBg + ")" }}
        ></div>
      </div>
      <div
        className="login"
        style={{
          minWidth: "80%",
          minHeight: "calc(100vh - 40px)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div className="row col-lg-12 justify-content-center">
          <div className="row col-lg-12 justify-content-center">
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/master-sales");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={userIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Daftar Sales</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/master-baki");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={bakiIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h2>
                    <strong>Daftar Baki</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/laporan-stock-jual");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={laporanIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h2>
                    <strong>Stock Jual</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
          </div>
          <div className="row col-lg-12 justify-content-center">
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/lihat-penjualan");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={reprintIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Reprint Penjualan</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/lihat-pembelian");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={reprintIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h2>
                    <strong>Reprint Pembelian</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                showModalTutupToko();
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={tutupTokoIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h2>
                    <strong>Tutup Toko</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
          </div>
          <div className="col-lg-12 justify-content-center row">
            <div className="col-lg-4"></div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => routeChange("/home")}
            >
              <Card
                className="border-0 card-inverse bg-danger text-white text-center m-b-10 shadow"
                style={{ borderRadius: 30 }}
              >
                <CardBody>
                  <h2>
                    <strong>BACK</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
            <div className="col-lg-4"></div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default SubMenuUtility;
