import React, { useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { PageSettings } from "../../config/page-settings";
import background from "../../assets/img/background.jpg";
import { Card, CardBody } from "reactstrap";
import penjualanIcon from "../../assets/img/penjualan.gif";
import pembelian from "../../assets/img/pembelian.gif";
import pesananIcon from "../../assets/img/pesanan.gif";
import serviceIcon from "../../assets/img/service.gif";
import hutangIcon from "../../assets/img/hutang.gif";
import kasIcon from "../../assets/img/kas.gif";

const activeBg = background;
const SubMenuLaporan = () => {
  const type = useContext(PageSettings);
  const history = useHistory();
  //   const [dateSystem, setdateSystem] = useState(true);
  const version = process.env.REACT_APP_VERSION;
  const routeChange = (path) => {
    history.push(path);
  };
  useEffect(() => {
    type.handleSetPageSidebar(false);
    type.handleSetPageHeader(false);
    // setdateSystem((getUserdata()?.tgl_system || "-") === getToday());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <React.Fragment>
      <div className="login-cover">
        <div
          className="login-cover-image"
          style={{ backgroundImage: "url(" + activeBg + ")" }}
        ></div>
      </div>
      <div
        className="login"
        style={{
          minWidth: "80%",
          minHeight: "calc(100vh - 40px)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div className="row col-lg-12 justify-content-center">
          <div className="row col-lg-12 justify-content-center">
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/laporan-transaksi-penjualan");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={penjualanIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Penjualan</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/laporan-transaksi-pembelian");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={pembelian}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Pembelian</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className={
                version === "full"
                  ? "col-lg-3 col-md-3"
                  : "col-lg-3 col-md-3 d-none"
              }
              onClick={() => {
                routeChange("/laporan-pesanan");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={pesananIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Pesanan</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className={
                version === "full" ? "col-lg-3 col-md-3 " : "col-lg-3 col-md-3 "
              }
              onClick={() => {
                routeChange("/laporan-barang-sumary");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={pesananIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Barang Summary</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className={
                version === "full"
                  ? "col-lg-3 col-md-3"
                  : "col-lg-3 col-md-3 d-none"
              }
              onClick={() => {
                routeChange("/laporan-services");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={serviceIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Service</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className={
                version === "full"
                  ? "col-lg-3 col-md-3"
                  : "col-lg-3 col-md-3 d-none"
              }
              onClick={() => {
                routeChange("/laporan-transaksi-hutang");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={hutangIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Hutang</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => {
                routeChange("/laporan-transaksi-keuangan-cash");
              }}
            >
              <Card
                style={{
                  borderRadius: 30,
                  backgroundColor: "#619680 !important",
                }}
                className="border-0 card-inverse bg-primary text-white text-center m-b-10 shadow"
              >
                <CardBody>
                  <img
                    src={kasIcon}
                    alt="this slowpoke moves"
                    width="150"
                    class="img-fluid"
                  />
                  <h3>
                    <strong>Laporan Kas</strong>
                  </h3>
                </CardBody>
              </Card>
            </div>
          </div>
          <div className="col-lg-12 justify-content-center row">
            <div className="col-lg-4"></div>
            <div
              className="col-lg-3 col-md-3"
              onClick={() => routeChange("/home")}
            >
              <Card
                className="border-0 card-inverse bg-danger text-white text-center m-b-10 shadow"
                style={{ borderRadius: 30 }}
              >
                <CardBody>
                  <h2>
                    <strong>BACK</strong>
                  </h2>
                </CardBody>
              </Card>
            </div>
            <div className="col-lg-4"></div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default SubMenuLaporan;
